nav {
  position: absolute;
  top: 0;
  left: 0;
  color: rgb(255, 255, 255);
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  border-bottom: 2px solid rgba(255, 255, 255, 0.183);
  font-size: 22px;
  box-shadow: 0 0 8px rgb(129, 129, 129);
  background-color: transparent;
}
nav ul.dropdown-menu {
  text-align: center;
  padding: 0;
  margin: 0;
  backdrop-filter: blur(12px);
  background-color: rgb(255, 255, 255);
}
.main-body-dark nav ul.dropdown-menu {
  background-color: rgb(0, 0, 0);
}
ul.dropdown-menu li{
  padding: 0;
  text-align: centerrgb(28, 28, 28)
}
.main-body-light .navbar-nav > li > .dropdown-menu a:hover { 
  background-color: rgba(221, 235, 247, 0.957);
}
.main-body-dark .navbar-nav > li > .dropdown-menu a:hover { 
  background-color: #45024ce7;
}
.hover-underline-animation {
  display: inline-block;
  position: relative;
}
.hover-underline-animation:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 4px;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.main-body-light .hover-underline-animation:after{
  background-color: #000000;
}
.hover-underline-animation:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
nav h3{
  width: auto;
  display: inline-block;
  text-transform: uppercase;
  margin: auto;
  letter-spacing: 5px;
}

.navbar-brand p{
  font-weight: 700;
  margin:0 auto;
  position: relative;
  overflow: hidden;
  background: linear-gradient(90deg, #000, #fff, #000);
  background-repeat: no-repeat;
  background-size: 80%;
  animation: paraAni 4s linear infinite;
  -webkit-background-clip: text;
  -webkit-text-fill-color: rgba(255, 255, 255, 0);
}
@keyframes paraAni {
  0% {
    background-position: -500%;
  }
  100% {
    background-position: 500%;
  }
}
.badge{
  font-size: 12px;
  background-color: rgb(241, 69, 22);
}
.navcart {
  display: inline-block;
  margin-right: 27px;
}

.navcart .active {
  font-weight: 900;
  background-color: transparent;
}

.navbar {
  background-color: rgba(40, 2, 73, 0.505);
  backdrop-filter: blur(12px);
  z-index: 999;
  /* overflow: hidden; */
  position: fixed;
  top: 0;
  height: 2.6em;
  min-width: 100%;
}
.main-body-light .navbar{
  backdrop-filter: blur(32px);
  background-color: rgba(177, 175, 165, 0.21);
}
.active {
  transition: all ease-in 0.5s;
  backdrop-filter: blur(32px);
  background-color: rgba(136, 136, 136, 0.281);
  border-radius: 100px;
  transition: all ease-out 0.5s;
}
nav button{
  font-size: 20px;
}
